  --base-color: #1d2d44;
  --sub-base-color: #3e5c76;
  --sub-color: #0d1321;
  --text-color: #f0ebd8;
  --base-p-fs: calc(0.8rem + 0.7vw);
  --base-h1-fs: calc(2rem + 2vw);
  --base-h2-fs: calc(1rem + 0.8vw);
  --base-h3-fs: calc(1.2rem + 1vw);
  --base-h1-ff: "Secular One", sans-serif;
  --base-h2-ff: "Assistant", sans-serif;
  --base-p-ff: "Volkhov", sans-serif;
  --logo-width: max(35px, 30%);
  --base-paragraph-ff: "Nanum Myeongjo", sans-serif;
  --base-margin: 0.2em 0.6em;
  --base-padding: 0.4em 0.6em;
  --box-shadow: 5px 10px 30px black;
}

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

body {
  height: 100vh;
  background-color: var(--sub-base-color);
}

/* PLAYER 1 INPUT STYLES */
.player1-name-input {
  background-color: var(--sub-color);
  display: grid;
  justify-items: center;
  align-items: center;
  gap: 10px;
  padding: var(--base-padding);
}

.player1-name-input label {
  font-size: var(--base-h2-fs);
  font-family: var(--base-h2-ff);
  color: var(--text-color);
}

.player1-name-input input {
  font-size: var(--base-p-fs);
  font-family: var(--base-p-ff);
  border-radius: 0.5em;
  border: none;
  padding-left: 0.4em;
}

.player1-name-input input:focus {
  outline: none;
}

.player1-name-input input:valid {
  outline: 2.5px solid green;
}

.player1-name-input input:invalid {
  outline: 2.5px solid red;
}

.player1-btn {
  background-color: orange;
  border: none;
  padding: 0.4em 2em;
  border-radius: 0.5em;
  cursor: pointer;
}

.player1-btn:hover {
  scale: 1.1;
  transition: 0.2s;
}

/* PLACE PLAYER1 SHIPS STYLES */
.place-player-1-ships {
  padding: var(--base-padding);
  display: grid;
  justify-items: center;
}

.player1-name-h3 {
  font-size: var(--base-h3-fs);
  font-family: var(--base-h2-ff);
  color: orange;
  display: none;
}

.player-1-empty-board {
  width: 50vmin;
  height: 50vmin;
  display: grid;
  grid-template-columns: repeat(10, 1fr);
  grid-template-rows: repeat(10, 0.1fr);
  gap: 1px;
}

.cell-div {
  background-color: var(--base-color);
  display: grid;
  justify-items: center;
  align-items: center;
}

.war-ship-img {
  width: max(25px, 25%);
}

.player1-btns {
  width: 22%;
  display: flex;
  justify-content: space-evenly;
  margin: var(--base-padding);
}

.rotate-ship {
  background-color: orange;
  border: none;
  color: var(--sub-color);
  padding: 0.4em 1em;
  border-radius: 0.5em;
  cursor: pointer;
  display: none;
}

.rotate-ship:hover {
  scale: 0.97;
  transition: 0.3s;
}

.play-game-btn {
  background-color: var(--base-color);
  border: none;
  color: var(--text-color);
  padding: 0.4em 1em;
  border-radius: 0.5em;
  cursor: pointer;
  display: none;
}

.play-game-btn:hover {
  scale: 0.97;
  transition: 0.3s;
}

/* GAME CONTAINER STYLES */
.game-container {
  justify-items: center;
  display: none;
}

.game-play-results {
  font-size: var(--base-h3-fs);
  font-family: var(--base-h2-ff);
  color: orange;
}

.board-container {
  width: 100%;
  padding: var(--base-padding);
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(400px, 1fr));
  gap: 20px;
  justify-items: center;
}

.player-1-board,
.player-2-board {
  display: grid;
  grid-template-columns: repeat(10, 1fr);
  grid-template-rows: repeat(10, 0.1fr);
  gap: 1px;
}

.game-cell {
  background-color: var(--base-color);
  display: grid;
  justify-items: center;
  align-items: center;
  width: 40px;
  height: 40px;
}

.skeleton-img {
  width: max(25px, 25%);
  filter: invert(99%) sepia(99%) saturate(0%) hue-rotate(310deg)
    brightness(110%) contrast(100%);
}

.play-again-btn {
  background-color: orange;
  border: none;
  color: var(--sub-color);
  padding: 0.4em 1em;
  border-radius: 0.5em;
  cursor: pointer;
  margin-top: 2.5em;
  display: none;
}

.play-again-btn:hover {
  scale: 1.1;
  transition: 0.3s;
}

@media (max-width: 500px) {
  .place-player-1-ships {
    padding: var(--base-padding);
    display: grid;
    justify-items: center;
  }

  .player1-name-h3 {
    font-size: var(--base-h3-fs);
    font-family: var(--base-h2-ff);
    color: orange;
    display: none;
  }

  .player-1-empty-board {
    width: 95vw;
    height: 35vh;
    display: grid;
    grid-template-columns: repeat(10, 1fr);
    grid-template-rows: repeat(10, 0.1fr);
    gap: 1px;
    position: relative;
  }

  .cell-div {
    background-color: var(--base-color);
    display: grid;
    justify-items: center;
    align-items: center;
  }

  .war-ship-img {
    width: max(20px, 25%);
  }

  .player1-btns {
    width: 80%;
    display: flex;
    justify-content: space-evenly;
    margin: var(--base-padding);
  }

  .rotate-ship {
    background-color: orange;
    border: none;
    color: var(--sub-color);
    padding: 0.4em 0.8em;
    border-radius: 0.5em;
    cursor: pointer;
    display: none;
  }

  .rotate-ship:hover {
    scale: 0.97;
    transition: 0.3s;
  }

  .play-game-btn {
    background-color: var(--base-color);
    border: none;
    color: var(--text-color);
    padding: 0.4em 0.8em;
    border-radius: 0.5em;
    cursor: pointer;
    display: none;
  }

  .play-game-btn:hover {
    scale: 0.97;
    transition: 0.3s;
  }
}
